.bigpics {
  color: white;
  height: 450px;
  background-image: url('/images/1.jpg');
}

.bigpics-wrapper {
  display: flex;
  margin: 0 auto;
  max-width: 75%;
  align-items: center;
  height: 100%;
}

.bigpics-content {
  display: flex;
  justify-content: space-between;
  width: 100%;
  @media (max-width: 950px) {
    flex-direction: column;
  }

  input {
    height: 40px;
    padding: 10px 19px;
    padding-right: 35px;
    border: 1px solid #e0e0e0;
    border-radius: 2px;
    color: #656565;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    width: 400px;
    background-image: url('/images/search-icon.png');
    background-size: 20px 20px;
    background-position: 370px center;
    background-repeat: no-repeat;
    &::placeholder {
      font-style: italic;
      opacity: 0.5;
    }
    &:hover,
     &:focus {
       box-shadow: 0px 3px 19px -6px rgba(0,0,0,0.67);
     }
     &:focus {
       outline: 0;
     }
     @media (max-width: 950px) {
       width: 100%;
       padding-right: 0;
       background-image: none;
     }
  }
}

.bigpics-content-right {
  background-color: rgba(0, 0, 0, 0.42);
  box-shadow: 0px 3px 19px 1px rgba(0,0,0,0.67);
  padding: 20px;
  height: 300px;
  overflow: auto;
  /* width */
  &::-webkit-scrollbar {
      width: 10px;
  }

  /* Track */
  &::-webkit-scrollbar-track {
      background: #f1f1f1;
  }

  /* Handle */
  &::-webkit-scrollbar-thumb {
      background: #888;
  }

  /* Handle on hover */
  &::-webkit-scrollbar-thumb:hover {
      background: #555;
  }
  p {
    margin: 2px;
  }
}



